<template>
  <div id='cy'>
    <div id='topology-toolbar' class='btn-group' data-toggle='buttons'>
      <label @click='toggleEdgeFilter' class='btn btn-default active btn-sm'>
        <input type='checkbox' value='link' checked> Link
      </label>
      <label @click='toggleEdgeFilter' class='btn btn-default active btn-sm'>
        <input type='checkbox' value='tunnel' checked> Tunnel
      </label>
      <label @click='toggleEdgeFilter' class='btn btn-default deactive btn-sm' disabled>
        <input type='checkbox' value='pw'> PW
      </label>
    </div>
    <div id='topology-chosen'>
      <select data-placeholder='Select link or tunnel to highlight' class='chosen-select-deselect'>
        <option value=''></option>
        <optgroup label='Links (12)'>
          <option>Dallas Cowboys</option>
          <option>New York Giants</option>
          <option>Philadelphia Eagles</option>
          <option>Washington Redskins</option>
        </optgroup>
        <optgroup label='Tunnels (20)'>
          <option>Chicago Bears</option>
          <option>Detroit Lions</option>
          <option>Green Bay Packers</option>
          <option>Minnesota Vikings</option>
        </optgroup>
        <optgroup label='Lsps (123)'>
          <option>Chicago Bears</option>
          <option>Detroit Lions</option>
          <option>Green Bay Packers</option>
          <option>Minnesota Vikings</option>
        </optgroup>
      </select>
    </div>
  </div>
</template>

<script>
import store from '../vuex/store'
import { mapGetters } from 'vuex'
import 'expose-loader?$!expose-loader?jQuery!jquery'
import cytoscape from 'cytoscape'
import panzoom from 'cytoscape-panzoom'
import autopanOnDrag from 'cytoscape-autopan-on-drag'
import contextMenus from 'cytoscape-context-menus'
import navigator from 'cytoscape-navigator'
import qtip from 'cytoscape-qtip'
import EventBus from './event-bus'
import coseBilkent from 'cytoscape-cose-bilkent'
import { HTTP } from '../http-common'

export default {
  mounted() {
    panzoom(cytoscape);
    autopanOnDrag(cytoscape);
    navigator(cytoscape);
    contextMenus(cytoscape, $);
    qtip(cytoscape);
    coseBilkent(cytoscape);

    this.init();

    store.subscribe((mutation, state) => {
      this.onUpdate(mutation.payload);
    });

    $('.chosen-select').chosen();
    $('.chosen-select-deselect').chosen({ allow_single_deselect: true });

    $('#topology-chosen').on('mousewheel', (event) => {
      if (event.originalEvent.wheelDelta > 0 || event.originalEvent.detail < 0) {
        console.log('up');
      } else {
        console.log('down');
      }

      event.preventDefault();
      event.stopPropagation();
      event.stopImmediatePropagation();
    });

    EventBus.$on('GRID_RESIZE', (width, height) => {
      $('.cytoscape-navigator').css('top', (height - 205) + 'px');
    });

    EventBus.$on('GRID_ITEM_SELECTED', (selectedItem) => {
      if (this.itemAnimationTimerId) {
        clearInterval(this.itemAnimationTimerId);
      }

      // if (this.edgesForPathDisplay.length > 0) {
      //   this.removeEdgesForPathDisplay();
      // }

      if (selectedItem.item.type === 'node') {
        let node = this.cy.filter(`node[id='${selectedItem.item.id}']`);
        this.itemAnimationTimerId = setInterval(() => {
          node.animate({ style: { 'border-width': 6, 'border-opacity': 0.2 } }, { duration: 400 })
            .animate({ style: { 'border-width': 1, 'border-opacity': 1 } }, { duration: 400 });
        }, 800);
      }

      if (selectedItem.item.type === 'link') {
        let edge = this.cy.filter(`edge[id='${selectedItem.item.id}']`);
        this.itemAnimationTimerId = setInterval(() => {
          edge.animate({ style: { 'width': 5 } }, { duration: 400 })
            .animate({ style: { 'width': 1 } }, { duration: 400 });
        }, 800);
      }

      if (selectedItem.item.id.indexOf('TUNNELTRAIL') > -1) {
        // 모든 링크를 hidden 한다.
        // requestTunnelInfo를 조회한다. (Tunneltrail id로)
        // 터널을 임의로 새로 만든다.
        // 구간별 LSP를 임의로 새로 만든다.
        // 해당 구간을 사용하는 링크를 찾아서 show 한다.
        // 레이블을 표시한다. ?
        // 포트를 표시한다. ?
        // 애니메이션이 필요한가? style에 css transition 사용
        // mouseup 하면 지워버린다.? rclick 하면 지워버린다?
        // 새로 만든애들 qtip 넣어줘야함
        // 화면에 표시한 상태에서 다른거 클릭하거나 해당 항목들 클릭하면 mouseup 이벤트가 뜨는데.. 어떡하냐..
        // flag를 세워서 rclick으로 해제하기 전에 다른 동작을 안되게하자. 버튼 필터링, 링크필터링, 아이템 클릭, 아래 메뉴 클릭 등.
        // 안되겠다. 다른방법을 찾아보자. 상단에 버튼을 만들던가.. 

        this.cy.nodes().connectedEdges().forEach((ele) => { ele.hide(); });

        let createLspDrawingInfo = function(type, lspNniPathInfo, lspPathCount) {
          if (lspPathCount === 0) {
            return [];
          }

          let edges = [];
          if (lspPathCount === 1) {
            edges.push({ data: { id: lspNniPathInfo.srcLinkId + '@', source: lspNniPathInfo.srcLinkSrcNodeId, target: lspNniPathInfo.srcLinkDstNodeId } });
          } else {
            edges.push({ data: { id: lspNniPathInfo.srcLinkId + '@', source: lspNniPathInfo.srcLinkSrcNodeId, target: lspNniPathInfo.srcLinkDstNodeId } });
            edges.push({ data: { id: lspNniPathInfo.dstLinkId + '@', source: lspNniPathInfo.dstLinkSrcNodeId, target: lspNniPathInfo.dstLinkDstNodeId } });

            if (lspNniPathInfo.hasOwnProperty('lspNniTransitInfo') && lspNniPathInfo.lspNniTransitInfo.length > 1) {
              // transit node가 2개 이상 있는 경우 링크 추가
            }
          }

          edges.forEach(function(element) {
            Object.assign(element, { group: 'edges', classes: `lsp_${type}` });
          });

          return edges;
        }

        HTTP.get('requestTunnel.json').then((response) => {
          let tunnelInfo = response.data.content.requestTunnelInfo.response;
          let edges = [];
          edges.push({
            group: 'edges',
            data: { id: tunnelInfo.id, source: tunnelInfo.wkLsp.srcNodeId, target: tunnelInfo.wkLsp.dstNodeId }, // 양 끝 노드 정보 필요
            classes: 'tunnel'
          });

          edges = edges.concat(createLspDrawingInfo('wk', tunnelInfo.wkLsp.lspNniPathInfo, 1 /* tunnelInfo.wkLsp.lspNniPathInfo.count */));
          edges = edges.concat(createLspDrawingInfo('pt', tunnelInfo.ptLsps[0].lspNniPathInfo, 2 /* tunnelInfo.wkLsp.lspNniPathInfo.count */));

          this.cy.batch(() => {
            edges.forEach(function(element) {
              if (element.classes !== 'tunnel') {
                this.cy.filter(`edge[id = "${element.data.id.slice(0, -1)}"]`).show();
              }
            }, this);

            let eles = this.cy.add(edges);
            eles.qtip({ content: () => { } });
          });

          // // 이미 그려진 노드와 링크는 배열에만 추가한다
          // let nodesIds = new Set();
          // let linksIds = new Set();
          // edges.forEach(function(element) {
          //   if (element.classes.indexOf('lsp') !== -1) {
          //     linksIds.add(element.data.id);
          //     nodesIds.add(element.data.source);
          //     nodesIds.add(element.data.target);
          //   }
          // });

          // nodesIds.forEach(function(id) {
          //   console.log(this.cy.filter(`node[id = "${id}"]`).data());
          //   edges.push(this.cy.filter(`node[id = "${id}"]`));
          // }, this);

          // linksIds.forEach(function(id) {
          //   edges.push(this.cy.filter(`edge[id = "${id.slice(0, -1)}"]`));
          // }, this);

          // this.edgesForPathDisplay = edges;
        }).catch((err) => {
          console.log(err);
          return;
        });
      }
    });
    // cy.on('ready', function() {
    //   updateBounds();
    // });
    // //if they resize the window, resize the diagram
    // $(window).resize(function() {
    //   updateBounds();
    // });

    // var updateBounds = function() {
    //   var bounds = cy.elements().boundingBox();
    //   $('#cyContainer').css('height', bounds.h + 300);
    //   cy.center();
    //   cy.resize();
    //   //fix the Edgehandles
    //   $('#cy').cytoscapeEdgehandles('resize');
    // };
  },
  data() {
    return {
      initLoad: false,
      showPortLabel: false,
      labelFormat: (item) => { return item.name },
      nodesPos: [],
      itemAnimationTimerId: 0
      // edgesForPathDisplay: []
    }
  },
  methods: {
    toggleEdgeFilter(event) {
      // if (this.edgesForPathDisplay.length > 0) {
      //   return;
      // }

      let isChecked = ($(event.target).find('input:checked').length === 0);
      let target = $(event.target).find('input').val();

      this.cy.batch(() => {
        this.cy.nodes().connectedEdges('.' + target).forEach((ele, i, eles) => {
          if (isChecked) {
            ele.show();
          } else {
            ele.hide();
          }
        });
      })
    },
    // removeEdgesForPathDisplay() {
    //   if (this.edgesForPathDisplay.length > 0) {
    //     this.cy.batch(() => {
    //       this.edgesForPathDisplay.forEach((element) => {
    //         // console.log(element);
    //         // if (element.group === 'nodes' || element.classes === 'link') {
    //         //   return;
    //         // }

    //         let edge = this.cy.filter(`edge[id = "${element.data.id}"]`);
    //         if (edge.length > 0) {
    //           this.cy.remove(edge);
    //         }
    //       }, this);

    //       this.cy.nodes().connectedEdges().forEach((ele) => { ele.show(); });
    //       this.edgesForPathDisplay = [];
    //     });
    //   }
    // },
    init() {
      let cy = this.cy = cytoscape(
        {
          container: document.getElementById('cy'),
          boxSelectionEnabled: true,
          autounselectify: false,
          wheelSensitivity: 0.2,
          elements: [],
          style: [ // the stylesheet for the graph
            {
              selector: 'node',
              style: {
                // 'shape': 'rectangle',
                'border-width': 1,
                'label': 'data(label)',
                // 'background-fit': 'none',
                // 'background-image-opacity': 1,
                // 'text-valign': 'center',
                // 'text-outline-width': 2,
                // 'text-outline-color': '#888',
                'border-color': '#455A64',
                'background-color': '#859AA4'
              }
            },
            {
              selector: 'edge',
              style: {
                'width': 'data(lineWidth)',
                'line-color': '#607D8B',
                'opacity': '1',
                // 'target-arrow-color': 'data(lineColor)',
                // 'target-arrow-shape': 'triangle',
                // 'haystack-radius': '0.5',
                // 'opacity': '0.4',
                // 'line-color': '#bbb',
                // 'width': 'mapData(1, 0, 1, 1, 8)',
                'source-label': 'data(labelSourcePort)',
                'target-label': 'data(labelTargetPort)',
                'source-text-offset': 22,
                'target-text-offset': 22,
                'font-size': 10
              }
            },
            {
              selector: '.link',
              style: {
                'curve-style': 'bezier',
                'control-point-step-size': 18,
                'overlay-padding': '2.5px'
              }
            },
            {
              selector: '.tunnel',
              style: {
                // 'source-endpoint': 'outside-to-node',
                // 'target-endpoint': 'outside-to-node',
                // 'source-arrow-shape': 'triangle-cross',
                // 'source-arrow-fill': 'hollow',
                // 'source-distance-from-node': '20px',
                // 'target-distance-from-node': '20px',
                'width': 20,
                'line-color': '#616161',
                'opacity': 0.25,
                'overlay-padding': '12px'
              }
            },
            {
              selector: '.lsp_wk',
              style: {
                'width': '3',
                'curve-style': 'unbundled-bezier',
                'source-distance-from-node': '10px',
                'target-distance-from-node': '10px',
                'target-arrow-shape': 'triangle',
                'target-arrow-color': '#FFC107',
                'line-style': 'solid',
                'line-color': '#FFC107',
                'opacity': 1
              }
            },
            {
              selector: '.lsp_pt',
              style: {
                'width': '3',
                'curve-style': 'unbundled-bezier',
                'source-distance-from-node': '20px',
                'target-distance-from-node': '20px',
                'target-arrow-shape': 'triangle',
                'target-arrow-color': '#512DA8',
                'line-style': 'solid',
                'line-color': '#512DA8',
                'opacity': 1
              }
            },
            {
              selector: ':selected',
              style: {
                'line-color': '#000',
                'background-color': '#000',
                // 'target-arrow-color': 'black',
                // 'source-arrow-color': 'black',
                'opacity': 1
              }
            },
            {
              selector: '.bar',
              style: {
                'width': '3',
                'curve-style': 'unbundled-bezier',
                'source-endpoint': '10px 20px',
                'target-endpoint': '-10px 20px',
                'line-style': 'dashed',
                'line-color': '#0F0',
                'target-arrow-shape': 'triangle',
                'target-arrow-color': '#f00',
                'opacity': 1
              }
            },
            {
              selector: '.icon_a',
              style: {
                // 'background-image': '../../static/image/icon_a.png'
              }
            },
            {
              selector: '.icon_b',
              style: {
                // 'background-image': '../../static/image/icon_b.png'
              }
            },
            {
              selector: '.icon_m',
              style: {
                // 'background-image': '../../static/image/icon_m.png'
              }
            }
          ],
          layout: {
            name: 'cose-bilkent'
          }
        },
      )

      cy.viewport({ zoom: 1, pan: { x: 0, y: 0 } });
      cy.minZoom(0.1);
      cy.maxZoom(10);

      cy.panzoom({
        minZoom: 0.1,
        maxZoom: 10,
        animateOnFit: () => { return true },
        fitAnimationDuration: 300
      });

      cy.autopanOnDrag({});

      cy.navigator({
        container: false, // can be a HTML or jQuery element or jQuery selector
        viewLiveFramerate: 0, // set false to update graph pan only on drag end; set 0 to do it instantly; set a number (frames per second) to update not more than N times per second
        thumbnailEventFramerate: 30, // max thumbnail's updates per second triggered by graph updates
        thumbnailLiveFramerate: 2, // max thumbnail's updates per second. Set false to disable
        dblClickDelay: 200, // milliseconds
        removeCustomContainer: true, // destroy the container specified by user on plugin destroy
        rerenderDelay: 0 // ms to throttle rerender updates to the panzoom for performance
      });

      let self = this;
      cy.contextMenus({
        menuItems: [
          {
            id: 'label_format_name',
            content: 'Change label [ NAME ]',
            tooltipText: 'Change label [ NAME ]',
            // image: { src: 'remove.svg', width: 12, height: 12, x: 6, y: 4 },
            selector: 'node',
            onClickFunction: (event) => {
              self.labelFormat = (item) => {
                return `${item.name}`;
              }
              self.onUpdate(store.state.elements);
            },
            coreAsWell: true
          },
          {
            id: 'label_format_ip',
            content: 'Change label [ IP ]',
            tooltipText: 'Change label [ IP ]',
            // image: { src: 'remove.svg', width: 12, height: 12, x: 6, y: 4 },
            selector: 'node',
            onClickFunction: (event) => {
              self.labelFormat = (item) => {
                return `${item.id}`;
              }
              self.onUpdate(store.state.elements);
            },
            coreAsWell: true
          },
          {
            id: 'label_format_name_ip',
            content: 'Change label [ NAME (IP) ]',
            tooltipText: 'Change label [ NAME (IP) ]',
            selector: 'node',
            onClickFunction: (event) => {
              self.labelFormat = (item) => {
                return `${item.name} (${item.id})`;
              }
              self.onUpdate(store.state.elements);
            },
            hasTrailingDivider: true,
            coreAsWell: true
          },
          {
            id: 'show_port_label',
            content: 'Toggle port label',
            tooltipText: 'Toggle port label',
            selector: 'node, edge',
            onClickFunction: (event) => {
              self.showPortLabel = !self.showPortLabel;
              self.onUpdate(store.state.elements);
            },
            coreAsWell: true
          }
        ]
      });

      let makeTooltip = function(type, elem) {
        let text = '';
        let data = elem.data();
        if (type === 'node') {
          text = `<b>ID</b>: ${data.id}<br><b>NAME</b>: ${data.name}<br><b>ME ID</b>: ${data.me}<br><b>PRODUCT NAME</b>: ${data.product}<br>`;
        }

        if (type === 'edge') {
          if (elem.hasClass('tunnel')) {
            text = `<b>TUNNELS</b>: ${data.count}<br><b>${data.source} - ${data.target}</b>`;
          } else {
            text = `<b>ID</b>: ${data.id}<br><b>NAME</b>: ${data.name}<br><b>SRC NODE</b>: ${data.source}<br><b>DST NODE</b>: ${data.target}
            <br><b>SRC PORT</b>: ${data.sourcePort}<br><b>DST PORT</b>: ${data.targetPort}<br><b>SRC PRODUCT NAME</b>: ${data.sourceProduct}
            <br><b>DST PRODUCT NAME</b>: ${data.targetProduct}<br><b>BANDWIDTH</b>: ${data.bandwidth}<br><b>MAX BANDWIDTH</b>: ${data.maxBandwidth}`;
          }
        }
        return text;
      }
      let showTootip = function(elem, text) {
        elem.qtip({
          content: text,
          position: {
            my: 'top center',
            at: 'bottom center'
          },
          show: {
            cyBgOnly: true
          },
          style: {
            classes: 'qtip-bootstrap'
          }
        })
      }

      cy.on('click', 'node', (event) => {
        let elem = event.target;
        elem.qtip('api').destroy();
        showTootip(elem, makeTooltip('node', elem));
      });

      cy.on('click', 'edge', (event) => {
        let elem = event.target;
        elem.qtip('api').destroy();
        showTootip(elem, makeTooltip('edge', elem));
      });

      // cy.on('boxselect', function(event) {
      //   cy.elements().forEach(function(ele, i, eles) {
      //     if (ele.selected()) {
      //       // console.log(ele.id());
      //     }
      //   });
      // });

      // cy.on('select', (event) => {
      //   this.removeEdgesForPathDisplay();
      // });

      // cy.on('cxttap', (event) => { 
      //   if (this.edgesForPathDisplay.length > 0) {
      //     this.removeEdgesForPathDisplay();
      //     event.preventDefault();
      //   }
      // });

      cy.on('mouseup', (event) => {
        // if (event.target === this.cy) {
        //   console.log(event);
        //   return;
        // }

        if (this.itemAnimationTimerId) {
          clearInterval(this.itemAnimationTimerId);
        }

        setTimeout(() => {
          let elements = [];
          cy.elements().forEach((ele, i, eles) => {
            if (ele.selected()) {
              elements.push(ele.data());
            }
          });

          // if (this.edgesForPathDisplay.length > 0) {
          //   console.log(this.edgesForPathDisplay);
          //   let diff = elements.filter(element => this.edgesForPathDisplay.map(x => x.data.id).indexOf(element.id) === -1);
          //   console.log(diff);
          //   if (diff.length === 0) {
          //     return;
          //   } else {
          //     this.removeEdgesForPathDisplay();
          //   }
          // }

          let unique = new Set(elements.map((e) => e.type));
          if (unique.size > 0) {
            let type = '';
            if (unique.size > 1) {
              type = '*';
            } else {
              type = unique.values().next().value;
            }

            if (self.itemAnimationTimerId) {
              clearInterval(self.itemAnimationTimerId);
            }

            EventBus.$emit('TOPOLOGY_ITEM_CHANGED', type, elements);
          }
        }, 100);
      });
    },
    onUpdate(elements) {
      let self = this;
      let cy = this.cy;
      cy.startBatch();

      // save node position
      self.nodesPos = [];
      if (cy.nodes().length > 0) {
        cy.nodes().forEach((ele) => {
          self.nodesPos.push({
            id: ele.id(),
            pos: ele.position()
          })
        });
      }

      cy.elements().remove();

      let nodes = [];
      for (let node of elements.nodes) {
        let iconMap = new Map([['91', 'icon_b'], ['92', 'icon_m'], ['93', 'icon_m'], ['94', 'icon_m'], ['95', 'icon_a']]);
        let newNodeIcon = iconMap.get(node.name.slice(0, 2));
        let newNode = {
          group: 'nodes',
          data: {
            id: node.id,
            name: node.name,
            type: 'node',
            label: self.labelFormat(node),
            me: node.extension.MeId,
            product: node.extension.ProductName,
            item: node
          },
          classes: (newNodeIcon === undefined) ? '' : newNodeIcon
        };

        let oldNodePos = self.nodesPos.find((nodePos) => {
          return nodePos.id === node.id;
        });

        if (oldNodePos) {
          Object.assign(newNode, { position: oldNodePos.pos });
        }
        nodes.push(newNode);
      }

      let links = [];
      for (let link of elements.links) {
        let nodesById = elements.nodes.filter((node) => {
          return (node.id === link.srcNodeId) || (node.id === link.dstNodeId);
        });

        if (nodesById.length >= 2) {
          let lableSourcePort = `${/(slot=)\d+/.exec(link.srcPortId)[0].replace('slot=', '')}/${/(port=)\d+/.exec(link.srcPortId)[0].replace('port=', '')}`;
          let labelTargetPort = `${/(slot=)\d+/.exec(link.dstPortId)[0].replace('slot=', '')}/${/(port=)\d+/.exec(link.dstPortId)[0].replace('port=', '')}`;
          links.push({
            group: 'edges',
            data: {
              id: link.id,
              name: link.name,
              type: 'link',
              label: link.name,
              labelSourcePort: (self.showPortLabel) ? lableSourcePort : '',
              labelTargetPort: (self.showPortLabel) ? labelTargetPort : '',
              source: link.srcNodeId,
              target: link.dstNodeId,
              sourcePort: link.srcPortId,
              targetPort: link.dstPortId,
              sourceProduct: link.srcProductName,
              targetProduct: link.dstProductName,
              bandwidth: link.bandwidth,
              maxBandwidth: link.maxBandwidth,
              item: link,
              lineWidth: (parseInt(link.bandwidth) <= 1000000) ? 0.5 : 1.5
            },
            classes: 'link'
          })
        }
      }

      let tunnelsBySection = [];
      for (let tunnel of elements.tunnels) {
        let origin = '10.10.10.11^10.10.10.12'; // tunnel.extenstion['srcNodeId'] + '^' + tunnel.extenstion['dstNodeId']
        let reverse = '10.10.10.12^10.10.10.11';
        let tunnels = tunnelsBySection.find((tunnels) => {
          return (tunnels.id === origin || tunnels.id === reverse);
        });

        if (tunnels !== undefined) {
          tunnels.childList.push(tunnel);
        } else {
          tunnelsBySection.push({
            id: origin,
            source: '10.10.10.11', // item.extenstion['srcNodeId']
            target: '10.10.10.12', // item.extenstion['dstNodeId']
            childList: [tunnel]
          });
        }
      }

      console.log(tunnelsBySection);

      tunnelsBySection.forEach((tunnels, index) => {
        links.push({
          group: 'edges',
          data: {
            id: 'tunnels' + index,
            name: '',
            type: 'tunnels',
            count: tunnels.childList.length,
            tunnels: tunnels.childList,
            source: tunnels.source,
            target: tunnels.target,
            item: tunnels
          },
          classes: 'tunnel'
        })
      });

      // 여기 고쳐야해
