      //   { 'group': 'edges', data: { 'id': 'edge' + this.count, 'source': 'node1', 'target': 'node2' } }
      // ]);

      if (!self.initLoad) {
        cy.center(self.cy.nodes());

        let layout = cy.layout({
          name: 'cose-bilkent',
          nodeDimensionsIncludeLabels: true,
          animate: 'end',
          animationEasing: 'ease-out',
          animationDuration: 500,
          padding: 50,
          tilingPaddingHorizontal: 30,
          randomize: true
        });

        layout.run();

        $('#topology-toolbar').css('visibility', 'visible');
        self.initLoad = true;
      }

      // register empty qtip for using qtip api
      cy.elements().qtip({
        content: () => { }
      })

      cy.endBatch();
    }
  }
}
</script>

<style>
@import '../../node_modules/cytoscape-panzoom/cytoscape.js-panzoom.css';
@import '../../node_modules/cytoscape-panzoom/font-awesome-4.0.3/css/font-awesome.min.css';
@import '../../node_modules/cytoscape-context-menus/cytoscape-context-menus.css';
@import '../../node_modules/cytoscape-navigator/cytoscape.js-navigator.css';
@import '../../node_modules/qtip2/dist/jquery.qtip.css';

#cy {
  width: 100%;
  height: 100%;
  position: relative;
  top: 0px;
  left: 0px;
  font-size: 12px;
  font-family: 'helvetica neue, helvetica, arial, sans-serif';
}

#topology-toolbar {
  position: absolute;
  z-index: 1;
  top: 1px;
  right: 410px;
  visibility: hidden;
}

#topology-chosen {
  position: absolute;
  z-index: 99999;
  top: 1px;
  right: 1px;
}

.cytoscape-navigator {
  position: fixed;
  border: 1px solid #e5e5e5;
  background: #fff;
  z-index: 99999;
  width: 200px;
  height: 200px;
  bottom: 0;
  right: 0;
  overflow: hidden;
}

.btn:focus,
.btn:active:focus,
.btn.active:focus,
.btn.focus,
.btn:active.focus,
.btn.active.focus {
  outline: none;
}

.qtip {
  max-width: 1000px;
  font-size: 11px;
  font-family: 'helvetica neue, helvetica, arial, sans-serif';
}

.chosen-select-deselect {
  width: 400px;
}

.chosen-container-single .chosen-single {
  height: 30px;
  line-height: 30px;
  font-size: 12px;
}
</style>


